"use client"; // Ensures it runs on the client-side

import { useEffect, useState } from "react";
import axios from "axios";
import Button from "@/components/ui/button"; // Default import
import Card from "@/components/ui/card"; // Default import
import Input from "@/components/ui/input"; // Default import

const ICMPProbes = () => {
  const [probes, setProbes] = useState([]);
  const [name, setName] = useState("");
  const [location, setLocation] = useState("");
  const [measurement, setMeasurement] = useState("icmp_probes");
  const [probeNameToUpdate, setProbeNameToUpdate] = useState("");
  const [newLocation, setNewLocation] = useState("");
  const [probeNameToDelete, setProbeNameToDelete] = useState("");

  const fetchProbes = async () => {
    try {
      const response = await axios.get(`${process.env.NEXT_PUBLIC_API_URL}/api/v1/icmp/list`);
      setProbes(response.data.probes);
    } catch (error) {
      console.error("Error fetching probes:", error);
    }
  };

  useEffect(() => {
    fetchProbes();
  }, []);

  const createProbe = async () => {
    try {
      const response = await axios.post(`http://127.0.0.1:8080/api/v1/icmp/create`, {
        name,
        location,
        measurement,
      });
      setProbes((prev) => [...prev, response.data]);
      setName("");
      setLocation("");
    } catch (error) {
      console.error("Error creating probe:", error);
    }
  };

  const updateProbe = async () => {
    try {
      await axios.post(`${process.env.NEXT_PUBLIC_API_URL}/api/v1/icmp/update`, {
        probe_name: probeNameToUpdate,
        location: newLocation,
      });
      setProbes((prev) =>
        prev.map((probe) =>
          probe.name === probeNameToUpdate ? { ...probe, location: newLocation } : probe
        )
      );
      setProbeNameToUpdate("");
      setNewLocation("");
    } catch (error) {
      console.error("Error updating probe:", error);
    }
  };

  const deleteProbe = async () => {
    try {
      await axios.post(`${process.env.NEXT_PUBLIC_API_URL}/api/v1/icmp/delete`, {
        name: probeNameToDelete,
      });
      setProbes((prev) => prev.filter((probe) => probe.name !== probeNameToDelete));
      setProbeNameToDelete("");
    } catch (error) {
      console.error("Error deleting probe:", error);
    }
  };

  return (
    <div className="container mx-auto p-6">
      <h1 className="text-2xl font-bold mb-6">ICMP Probes</h1>

      {/* Create Probe */}
      <div className="mb-6">
        <h2 className="text-xl font-semibold mb-2">Create Probe</h2>
        <div className="flex flex-col gap-2">
          <Input placeholder="Name" value={name} onChange={(e) => setName(e.target.value)} />
          <Input placeholder="Location" value={location} onChange={(e) => setLocation(e.target.value)} />
          <Button onClick={createProbe}>Create Probe</Button>
        </div>
      </div>

      {/* Update Probe */}
      <div className="mb-6">
        <h2 className="text-xl font-semibold mb-2">Update Probe</h2>
        <div className="flex flex-col gap-2">
          <Input
            placeholder="Probe Name"
            value={probeNameToUpdate}
            onChange={(e) => setProbeNameToUpdate(e.target.value)}
          />
          <Input
            placeholder="New Location"
            value={newLocation}
            onChange={(e) => setNewLocation(e.target.value)}
          />
          <Button onClick={updateProbe}>Update Probe</Button>
        </div>
      </div>

      {/* Delete Probe */}
      <div className="mb-6">
        <h2 className="text-xl font-semibold mb-2">Delete Probe</h2>
        <div className="flex flex-col gap-2">
          <Input
            placeholder="Probe Name"
            value={probeNameToDelete}
            onChange={(e) => setProbeNameToDelete(e.target.value)}
          />
          <Button onClick={deleteProbe} variant="destructive">Delete Probe</Button>
        </div>
      </div>

      <ul>
        {probes.map((probe) => (
          <Card key={probe.id} className="mb-2">
            <p>{probe.name} - {probe.location}</p>
          </Card>
        ))}
      </ul>
    </div>
  );
};

export default ICMPProbes;